// Smooth scroll
@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}

/* Style refresh */
h1,
h2,
h3,
h4,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

/* MAIN */
img {
  display: block;
  width: 100%;
  height: auto;
}

body {
  margin: 0;
  height: 100%;
}

button {
  border: none;
}

.title {
  text-align: center;
  // color: var(--title-text-color);
}

a {
  color: currentColor;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

// a:hover,
// a:focus {
//   // color: #f09930;
// }

address {
  font-style: normal;
}

/* Ð£Ð‘Ð˜Ð ÐÐ•Ðœ ÐŸÐžÐ”Ð§Ð•Ð ÐšÐ˜Ð’ÐÐÐ˜Ð• */
.decoration {
  text-decoration: none;
}

/* Ð£Ð‘Ð˜Ð ÐÐ•Ð¢ Ð­Ð¤Ð¤Ð•ÐšÐ¢ Ð¡ÐŸÐ˜Ð¡ÐšÐ */
.list {
  list-style: none;
}

/* ÐÐÐ¡Ð¢Ð ÐÐ˜Ð’ÐÐ•Ðœ ÐŸÐ ÐÐ’Ð˜Ð›Ð¬ÐÐ«Ð¹ Ð ÐÐ—ÐœÐ•Ð  Ð‘Ð›ÐžÐšÐžÐ’ */

html {
  box-sizing: border-box;
}

/* ÐÐÐ¡Ð›Ð•Ð”ÐžÐ’ÐÐÐ˜Ð• */

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Ð¡ÐŸÐ Ð¯Ð¢ÐÐÐÐ«Ð• Ð­Ð›Ð›Ð•ÐœÐ•ÐÐ¢Ð« */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}
